import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import Table from "../../components/Table/Table";
import { StoreState } from "../../reducers";
import {
  PostRequestStatusInterface,
  PostSchoolInterface,
  SchoolsInterface,
} from "../../shared/interfaces";
import {
  fetchSchoolPosts,
  createSchoolPost,
  getSchools,
  updateSchoolPost,
} from "../../actions/schools";
import { capitalizeFirstLetter } from "../../shared/dataTest";
import { GiTeacher } from "react-icons/gi";
import { Auth, PushPopupAlert } from "../../actions";
import { PopupAlertData } from "../../components/Alert/Popup";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { PositionAPI } from "../../interfaces";
import { getPositions } from "../../actions/settings";
import axios from "axios";
import { API } from "../../utils/api";
import { errorToText } from "../../utils/errors";
import { getSchoolById } from "../district/Schools";
import StepsHeader from "./StepsHeader";
import ViewPosition from "../SystemSettings/Positions/ViewPosition";
import { BiReply } from "react-icons/bi";

interface AppProps {
  auth: Auth;
  schools: SchoolsInterface[];
  systemPositions: PositionAPI[] | null;
  schoolPosts: PostSchoolInterface[];
  getPositions: Function;
  fetchSchoolPosts: Function;
  createSchoolPost: Function;
  updateSchoolPost: Function;
  getSchools: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

const RequestTeacher: React.FC<AppProps> = (props) => {
  const [loadingPosition, setLoadingPosition] = useState<boolean>(false);
  const [loadingPost, setLoadingPost] = useState<boolean>(true);
  const [modalState, setModalState] = useState<
    "CREATE" | "UPDATE" | "RECEIVE" | null
  >(null);
  const [filterByPosition, setFilterByPosition] = useState<string>("all");
  const [positionId, setPositionId] = useState<string>("");
  const [requestedPosts, setRequestedPosts] = useState<number>(0);
  const [femaleOnPosition, setFemaleOnPosition] = useState<number>(0);
  const [maleOnPosition, setMaleOnPosition] = useState<number>(0);
  const [requestExplanation, setRequestExplanation] = useState<string>("");
  const [loadingRequest, setLoadingRequest] = useState<boolean>(false);
  const [loading, setLoading] = useState<boolean>(true);
  const [formSteps, setFormSteps] = useState<{
    ONE: boolean;
    TWO: boolean;
  }>({ ONE: true, TWO: false });
  const [stepState, setStepState] = useState<{
    ONE: boolean;
    TWO: boolean;
  }>({ ONE: false, TWO: false });
  const [thisPost, setThisPost] = useState<PostSchoolInterface | null>(null);
  const [viewPost, setViewPost] = useState<boolean>(false);
  const [postId, setPostId] = useState<string>("");
  const [receivedPosts, setReceivedPosts] = useState<number>(0);
  const [formError, setFormError] = useState<{
    element: string;
    msg: string;
  } | null>(null);

  useEffect(() => {
    if (!props.schools.length) {
      props.getSchools((status: boolean, msg: string) => {
        setLoading(status);
        if (msg.length > 0) {
          console.error({ loadingCandidatesError: msg });
        }
      });
    } else {
      setLoading(false);
    }

    if (props.systemPositions === null) {
      setLoadingPosition(true);
      props.getPositions((status: boolean, msg: string) => {
        setLoadingPosition(status);
        console.log({ status: status, msg: msg });
        if (msg.length) {
          props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    }
    if (!props.schoolPosts.length && loadingPost) {
      props.fetchSchoolPosts(
        props.auth.user_access.school_id,
        (status: boolean, message: string) => {
          if (!status) {
            if (message.length > 0 && loadingPost) {
              console.error("error loading school post: ", message);
              props.PushPopupAlert({
                title: message,
                theme: PopupAlertTheme.danger,
              });
            }
            setLoadingPost(false);
          }
        }
      );
    } else {
      setLoadingPost(false);
    }
  }, [loadingPost, props]);

  const GetAllowedPositions = (position_id?: string): PositionAPI[] => {
    if (props.systemPositions) {
      if (props.schoolPosts.length > 0) {
        let postPositionIds: string[] = [];
        for (const key in props.schoolPosts) {
          if (Object.prototype.hasOwnProperty.call(props.schoolPosts, key)) {
            if (
              props.schoolPosts[key].requested_post_status !==
              PostRequestStatusInterface.RECEIVED
            ) {
              if (
                !position_id ||
                position_id !== props.schoolPosts[key].position_id
              ) {
                postPositionIds.push(props.schoolPosts[key].position_id);
              }
            }
          }
        }
        if (position_id) {
          postPositionIds = postPositionIds.filter(
            (item) => item !== position_id
          );
        }
        return props.systemPositions.filter(
          (item) => !postPositionIds.includes(item.position_id)
        );
      }
      return props.systemPositions;
    }
    return [];
  };

  const getPositionById = (
    systemPositions: PositionAPI[],
    positionId: string
  ): PositionAPI => {
    return systemPositions.filter((item) => item.position_id === positionId)[0];
  };

  if (props.auth.user_access.school_id.length <= 0) {
    return <Redirect to="/" />;
  }

  if (loading === true) {
    return (
      <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
        Loading data...
      </div>
    );
  }

  const tableData = (): JSX.Element[] => {
    if (loadingPost) {
      return [
        <tr key={1} className="border-b border-gray-200 hover:bg-gray-100">
          <td className="p-3 text-center" colSpan={7}>
            Loading post
          </td>
        </tr>,
      ];
    }

    let data = props.schoolPosts;

    data =
      filterByPosition !== "" && filterByPosition !== "all"
        ? data.filter(
            (schoolPost) => schoolPost.position_id === filterByPosition
          )
        : data;

    if (data.length === 0) {
      return [
        <tr key={1} className="border-b border-gray-200 hover:bg-gray-100">
          <td className="p-3 text-center" colSpan={7}>
            No Data Found
          </td>
        </tr>,
      ];
    }

    return data.map((schoolPost, index) => (
      <tr
        key={index + 1}
        className="border-b border-gray-200 hover:bg-gray-100"
      >
        <td className="py-3 px-4 text-left font-medium">
          {props.systemPositions &&
            getPositionById(props.systemPositions, schoolPost.position_id)
              .position_title}
        </td>
        <td className="py-3 px-4 text-left text-base font-medium">
          {schoolPost.needed_posts}
        </td>
        <td className="py-3 px-4 text-left text-base font-medium">
          {schoolPost.requested_post_status ===
          PostRequestStatusInterface.CONFIRMED ? (
            <div className="flex items-center gap-1">
              {schoolPost.confirmed_posts}
              <BiReply
                className="bg-yellow-600 w-5 h-5 text-white cursor-pointer"
                onClick={() => {
                  setModalState("RECEIVE");
                  setThisPost(schoolPost);
                }}
              />
            </div>
          ) : (
            "-"
          )}
        </td>
        <td className="py-3 px-4 text-left text-base font-medium">
          {schoolPost.occupied_posts ? schoolPost.occupied_posts : "-"}
        </td>
        <td className="py-3 px-4 text-left text-base font-medium">
          {schoolPost.requested_post_status ===
          PostRequestStatusInterface.RECEIVED
            ? schoolPost.received_posts
            : "-"}
        </td>
        <td>
          <span
            className={`px-2 py-1 font-semibold rounded ${
              schoolPost.requested_post_status ===
              PostRequestStatusInterface.INITIATED
                ? "text-gray-700 bg-gray-100 dark:bg-gray-700 dark:text-gray-100"
                : schoolPost.requested_post_status ===
                  PostRequestStatusInterface.CONFIRMED
                ? "text-blue-700 bg-blue-100 dark:bg-blue-700 dark:text-blue-100"
                : "text-green-800 bg-green-100 dark:bg-green-800 dark:text-green-100"
            }`}
          >
            {schoolPost.requested_post_status ===
            PostRequestStatusInterface.INITIATED
              ? "Pending"
              : capitalizeFirstLetter(schoolPost.requested_post_status)}
          </span>
        </td>
        <td className="py- text-left">
          <div className="flex item-center justify-end gap-1">
            <button
              className="
                btn
                btn-blue
                transition
                ease-out
                duration-500
                flex items-center
              "
              title="View User Profile"
              onClick={() => {
                setThisPost(schoolPost);
                setViewPost(true);
              }}
            >
              Details
            </button>
            {schoolPost.requested_post_status ===
              PostRequestStatusInterface.INITIATED && (
              <button
                className="btn bg-gray-500 text-white hover:bg-gray-700"
                onClick={() => {
                  setModalState("UPDATE");
                  setPostId(schoolPost.requested_post_id);
                  setPositionId(schoolPost.position_id);
                  setRequestedPosts(schoolPost.needed_posts);
                  setRequestExplanation(schoolPost.description);
                  setFemaleOnPosition(schoolPost.female_number);
                  setMaleOnPosition(schoolPost.male_number);
                  setFormSteps({ ONE: true, TWO: false });
                  setStepState({ ONE: false, TWO: false });
                }}
                disabled={loadingRequest}
              >
                Edit
              </button>
            )}
          </div>
        </td>
      </tr>
    ));
  };

  const submitRequest = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (modalState === "RECEIVE" && receivedPosts < 0) {
      return setFormError({
        element: "received_posts",
        msg: "Invalid number of received post",
      });
    } else if (
      modalState === "RECEIVE" &&
      thisPost &&
      receivedPosts > thisPost.needed_posts
    ) {
      return setFormError({
        element: "received_posts",
        msg: "Invalid number of received post",
      });
    }

    setLoadingRequest(true);
    try {
      if (modalState === "CREATE") {
        const res = await axios.post<{ data: PostSchoolInterface }>(
          `${API.PLACEMENT}/requestpost`,
          {
            school_id: props.auth.user_access.school_id,
            position_id: positionId,
            district_id: getSchoolById(
              props.schools,
              props.auth.user_access.school_id
            ).district_id,
            needed_posts: requestedPosts,
            male_number: maleOnPosition,
            female_number: femaleOnPosition,
            description: 34,
            // description: requestExplanation,
            occupied_posts: Number(maleOnPosition) + Number(femaleOnPosition),
            requested_by: props.auth.user.user_id,
          }
        );
        if (res) {
          props.createSchoolPost(res.data.data);
          setPositionId("");
          setRequestedPosts(0);
          setFemaleOnPosition(0);
          setMaleOnPosition(0);
          setRequestExplanation("");
          setFormSteps({ ONE: true, TWO: false });
          setStepState({ ONE: false, TWO: false });
          props.PushPopupAlert({
            title: `Post(s) has been requested!`,
            theme: PopupAlertTheme.success,
          });
        }
      } else if (modalState === "UPDATE") {
        console.log({
          district_id: getSchoolById(
            props.schools,
            props.auth.user_access.school_id
          ).district_id,
        });
        const res = await axios.patch<{ data: PostSchoolInterface }>(
          `${API.PLACEMENT}/requestpost/${postId}`,
          {
            confirmed_posts: 0,
            confirmed_by: null,
            received_posts: 0,
            approval_date: null,
            school_id: props.auth.user_access.school_id,
            position_id: positionId,
            district_id: getSchoolById(
              props.schools,
              props.auth.user_access.school_id
            ).district_id,
            needed_posts: requestedPosts,
            male_number: maleOnPosition,
            female_number: femaleOnPosition,
            description: requestExplanation,
            occupied_posts: Number(maleOnPosition) + Number(femaleOnPosition),
            requested_by: props.auth.user.user_id,
            requested_post_status: PostRequestStatusInterface.INITIATED,
          }
        );
        if (res) {
          // props.updateSchoolPost("SCHOOL", res.data.data);
          props.fetchSchoolPosts(
            props.auth.user_access.school_id,
            (status: boolean, message: string) => {
              if (!status) {
                if (message.length > 0 && loadingPost) {
                  console.error("error loading school post: ", message);
                  props.PushPopupAlert({
                    title: message,
                    theme: PopupAlertTheme.danger,
                  });
                }
                setLoadingPost(false);
              }
            }
          );
          thisPost && setThisPost(res.data.data);
          setPositionId("");
          setPostId("");
          setRequestedPosts(0);
          setFemaleOnPosition(0);
          setMaleOnPosition(0);
          setRequestExplanation("");
          setModalState(null);
          setFormSteps({ ONE: true, TWO: false });
          setStepState({ ONE: false, TWO: false });
          props.PushPopupAlert({
            title: `Post(s) has been updated!`,
            theme: PopupAlertTheme.info,
          });
        }
      } else if (modalState === "RECEIVE" && thisPost) {
        const res = await axios.patch<{ data: PostSchoolInterface }>(
          `${API.PLACEMENT}/requestpost/${thisPost.requested_post_id}`,
          {
            confirmed_posts: thisPost.confirmed_posts,
            confirmed_by: props.auth.user.user_id,
            received_posts: receivedPosts,
            approval_date: thisPost.approval_date,
            school_id: thisPost.school_id,
            position_id: thisPost.position_id,
            district_id: thisPost.district_id,
            needed_posts: thisPost.needed_posts,
            male_number: thisPost.male_number,
            female_number: thisPost.female_number,
            description: thisPost.description,
            occupied_posts: thisPost.occupied_posts,
            requested_by: props.auth.user.user_id,
            requested_post_status: PostRequestStatusInterface.RECEIVED,
          }
        );
        if (res) {
          // props.updateSchoolPost("SCHOOL", res.data.data);
          props.fetchSchoolPosts(
            props.auth.user_access.school_id,
            (status: boolean, message: string) => {
              if (!status) {
                if (message.length > 0 && loadingPost) {
                  console.error("error loading school post: ", message);
                  props.PushPopupAlert({
                    title: message,
                    theme: PopupAlertTheme.danger,
                  });
                }
                setLoadingPost(false);
              }
            }
          );

          !viewPost && setThisPost(null);
          setReceivedPosts(0);
          setModalState(null);
          props.PushPopupAlert({
            title: `Post request has been received!`,
            theme: PopupAlertTheme.info,
          });
        }
      }
    } catch (error) {
      console.error({ role_error: error });
      props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
    setLoadingRequest(false);
  };

  return (
    <React.Fragment>
      {/* <div className="bg-hero my-5 rounded p-4 flex justify-between items-center relative">
        <div className="flex flex-col">
          <span className="text-white text-4xl">User Management</span>
        </div>
      </div> */}
      {/* header title */}
      <div className="flex flex-row items-start gap-4 my-2">
        <GiTeacher className="text-blue-700 w-12 h-12 md:block hidden" />
        <div className="w-full">
          <div className="flex justify-between w-full">
            <div className="flex flex-col gap-1 justify-between">
              <h1 className="md:text-4xl text-3xl font-semibold">
                Teacher Request
              </h1>

              <div className="flex-row items-center md:gap-3 gap-2 hidden">
                <div className="flex flex-row items-center mt-1 gap-1">
                  <span className="text-gray-600 text-xl">level:</span>
                  {/* <span
                    className={`${
                      thisEducation ? "text-blue-600" : "text-gray-600"
                    } text-xl font-bold`}
                  >
                    {thisEducation
                      ? thisEducation.education_category_title
                      : "-"}
                  </span> */}
                  {/* {thisRoom && (
                      <div className="flex flex-row items-center gap-1">
                        <MdKeyboardArrowRight />
                        <span className={`text-blue-600 text-lg font-bold`}>
                          {thisRoom.room_name}
                        </span>
                      </div>
                    )} */}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      {/* end header title */}
      <div className="grid md:grid-cols-5 gap-2 bg-white shadow-md p-2">
        <div className={`md:col-span-5 rounded`}>
          <div className="my-2 flex sm:flex-row flex-col justify-between">
            <div className="flex flex-col md:flex-row">
              <select
                className="border-2 border-gray-700 bg-white p-2 rounded focus:outline-none"
                onChange={(e) => setFilterByPosition(e.target.value)}
              >
                <option value="all">All Position</option>
                {props.systemPositions && props.systemPositions.length > 0 ? (
                  props.systemPositions.map((position, index) => (
                    <option key={index + 1} value={position.position_id}>
                      {position.position_title}
                    </option>
                  ))
                ) : (
                  <option value="">No Position found</option>
                )}
              </select>
            </div>
            <div className="block relative">
              <div className="pt-6 md:pt-0 md:pl-6">
                <button
                  className="btn bg-primary text-white hover:bg-blue-600"
                  onClick={() => setModalState("CREATE")}
                  disabled={loadingPost || loadingPosition || loadingRequest}
                >
                  Request Teacher
                </button>
              </div>
            </div>
          </div>
          <Table
            columns={[
              { columnTitle: "POSITION", alignText: "left", padding: "p-3" },
              { columnTitle: "REQUEST", alignText: "left", padding: "p-3" },
              { columnTitle: "APPROVED", alignText: "left", padding: "p-3" },
              { columnTitle: "OCCUPIED", alignText: "left", padding: "p-3" },
              { columnTitle: "VACCANTS", alignText: "left", padding: "p-3" },
              { columnTitle: "STATUS", alignText: "left", padding: "p-3" },
              { columnTitle: "", alignText: "left", padding: "p-3" },
            ]}
            rows={tableData}
            pagination={true}
            search={true}
            thColor="gray"
          />
        </div>
      </div>

      {/* CREATE & UPDATE POST REQUEST Modal */}
      {(modalState === "CREATE" || modalState === "UPDATE") && (
        <Modal
          backDrop={true}
          theme={Themes.default}
          close={(): void => {
            setPositionId("");
            setRequestedPosts(0);
            setFemaleOnPosition(0);
            setMaleOnPosition(0);
            setRequestExplanation("");
            setModalState(null);
            setFormSteps({ ONE: true, TWO: false });
            setStepState({ ONE: false, TWO: false });
          }}
          displayClose={true}
          backDropClose={false}
          title={
            <div className="flex flex-row gap-4 items-center">
              {/* <IoBriefcase className="text-5xl text-gray-500" /> */}
              <div className="flex flex-col">
                <span className="font-bold text-2xl text-gray-500">
                  You are applying for Requesting Teachers
                </span>
              </div>
            </div>
          }
          widthSizeClass={ModalSize.extraLarge}
          padding={{ title: true, body: false, footer: false }}
        >
          <StepsHeader
            currentStep={formSteps.TWO ? 2 : 1}
            stepState={stepState}
            setFormSteps={setFormSteps}
          />
          <div className="px-4 mx-auto mb-4 mt-3">
            {formSteps.ONE && (
              <section>
                <div className="flex flex-col">
                  <label>Select Position</label>
                  <select
                    className="border-2 border-gray-700 bg-white p-2 rounded focus:outline-none focus:bg-gray-100"
                    value={positionId}
                    onChange={(e) => {
                      setPositionId(e.target.value);
                    }}
                  >
                    <option value="">Choose Position</option>
                    {props.systemPositions &&
                    props.systemPositions.length > 0 ? (
                      GetAllowedPositions(positionId).map((position, index) => (
                        <option key={index + 11} value={position.position_id}>
                          {position.position_title}
                        </option>
                      ))
                    ) : (
                      <option>No position found</option>
                    )}
                  </select>
                </div>
                {positionId.length > 0 && props.systemPositions && (
                  <div className="my-2 rounded border">
                    <ViewPosition
                      thisPosition={getPositionById(
                        props.systemPositions,
                        positionId
                      )}
                    />
                  </div>
                )}
                <div className="bg-white w-full flex justify-end">
                  {positionId.length > 0 && (
                    <button
                      className="btn btn-blue m-1 mr-2 mb-2 animate__animated animate__fadeIn focus:outline-none"
                      onClick={() => {
                        setFormSteps({
                          ONE: false,
                          TWO: true,
                        });
                        setStepState({
                          ONE: true,
                          TWO: false,
                        });
                      }}
                    >
                      Continue
                    </button>
                  )}
                </div>
              </section>
            )}

            <form onSubmit={submitRequest}>
              <div className="bg-white">
                {formSteps.TWO && (
                  <div className="text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7">
                    <div className="mb-4 md:flex md:justify-between">
                      <div className="mb-4 md:mr-2 md:mb-0 w-full">
                        <label
                          className="block mb-2 text-sm font-bold text-gray-700"
                          htmlFor="female"
                        >
                          Current number of Female on this position
                        </label>
                        <input
                          className="appearance-none rounded w-full py-1 px-4 border-2 border-gray-700 bg-white focus:outline-none focus:bg-gray-100"
                          id="female"
                          type="number"
                          min={0}
                          value={femaleOnPosition}
                          onChange={(e) =>
                            setFemaleOnPosition(Number(e.target.value))
                          }
                          placeholder="Current number of Female on this position"
                        />
                        {/* <p className="text-xs italic text-red-500">
                          Please choose a password.
                        </p> */}
                      </div>
                      <div className="md:ml-2 w-full">
                        <label
                          className="block mb-2 text-sm font-bold text-gray-700"
                          htmlFor="male"
                        >
                          Current number of Male on this position
                        </label>
                        <input
                          className="appearance-none rounded w-full py-1 px-4 border-2 border-gray-700 bg-white focus:outline-none focus:bg-gray-100"
                          id="male"
                          type="number"
                          min={0}
                          value={maleOnPosition}
                          onChange={(e) =>
                            setMaleOnPosition(Number(e.target.value))
                          }
                          placeholder="Current number of Male on this post"
                        />
                      </div>
                    </div>
                    {/* <div className="flex flex-col">
                      <label>Select Position</label>
                      <select
                        className="border-2 border-gray-700 bg-white p-2 rounded focus:outline-none focus:bg-gray-100"
                        // defaultValue={}
                        onChange={(e) => {
                          setPositionId(e.target.value);
                          setQualifacation(
                            props.systemPositions
                              ? getPositionById(
                                  props.systemPositions,
                                  e.target.value
                                ).requirements[0].position_requirement[0].title
                              : ""
                          );
                        }}
                        required
                      >
                        <option value="">Choose Position</option>
                        {props.systemPositions &&
                        props.systemPositions.length > 0 ? (
                          GetAllowedPositions().map((position, index) => (
                            <option
                              key={index + 11}
                              value={position.position_id}
                            >
                              {position.position_title}
                            </option>
                          ))
                        ) : (
                          <option value="">No position found</option>
                        )}
                      </select>
                    </div>
                    <div className="flex flex-col">
                      <label>Qualification</label>
                      <input
                        type="text"
                        className="appearance-none rounded w-full py-1 px-4 border-2 border-gray-700 bg-white focus:outline-none focus:bg-gray-100"
                        placeholder="Qualification"
                        value={qualification}
                        disabled
                      />
                    </div> */}
                    <div className="flex flex-col">
                      <label>Needed posts (number)</label>
                      <input
                        type="number"
                        className="appearance-none rounded w-full py-1 px-4 border-2 border-gray-700 bg-white focus:outline-none focus:bg-gray-100"
                        value={requestedPosts}
                        onChange={(e) =>
                          setRequestedPosts(Number(e.target.value))
                        }
                        min={1}
                        placeholder="1"
                        required
                      />
                    </div>
                    <div className="flex flex-col">
                      <label>Explanation</label>
                      <textarea
                        name="description"
                        className="appearance-none rounded w-full py-1 px-4 border-2 border-gray-700 bg-white focus:outline-none focus:bg-gray-100"
                        id="description"
                        placeholder="Explanation"
                        value={requestExplanation}
                        onChange={(e) => setRequestExplanation(e.target.value)}
                        rows={2}
                        required
                      ></textarea>
                    </div>
                  </div>
                )}
                <div className="pt-4 flex justify-between">
                  <button
                    className="btn bg-gray-400 text-white hover:bg-gray-600"
                    onClick={() => {
                      setPositionId("");
                      setRequestedPosts(0);
                      setFemaleOnPosition(0);
                      setMaleOnPosition(0);
                      setRequestExplanation("");
                      setModalState(null);
                      setFormSteps({ ONE: true, TWO: false });
                      setStepState({ ONE: false, TWO: false });
                    }}
                  >
                    Exit
                  </button>
                  {formSteps.TWO && (
                    <button
                      className="btn bg-primary text-white hover:bg-blue-600"
                      disabled={loadingRequest}
                    >
                      {loadingRequest ? "Loading..." : "Request"}
                    </button>
                  )}
                </div>
              </div>
            </form>
          </div>
        </Modal>
      )}
      {/* End of CREATE & UPDATE POST REQUEST Modal */}

      {/* Upload Modal */}
      {modalState === "RECEIVE" && thisPost && props.systemPositions && (
        <Modal
          backDrop={true}
          theme={Themes.default}
          close={(): void => {
            setThisPost(null);
            setModalState(null);
          }}
          displayClose={true}
          backDropClose={false}
          title={
            <div className="flex flex-row gap-4 items-center">
              {/* <IoBriefcase className="text-5xl text-gray-500" /> */}
              <div className="flex flex-col">
                <span className="font-bold text-2xl text-gray-500">
                  Responding to Requesting Teachers
                </span>
              </div>
            </div>
          }
          widthSizeClass={ModalSize.medium}
          padding={{ title: true, body: false, footer: false }}
        >
          <div className="px-4 mx-auto mb-4 mt-3">
            <form onSubmit={submitRequest}>
              <div className="bg-white">
                <div className="text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7">
                  <div className="flex flex-col">
                    <label>Position</label>
                    <input
                      type="text"
                      className="appearance-none rounded w-full py-1 px-4 border-2 border-gray-700 bg-white focus:outline-none focus:bg-gray-100"
                      value={
                        getPositionById(
                          props.systemPositions,
                          thisPost.position_id
                        ).position_title
                      }
                      placeholder="position title"
                      disabled
                    />
                  </div>
                  <div className="flex flex-col">
                    <label>Needed posts (number)</label>
                    <input
                      type="number"
                      className="appearance-none rounded w-full py-1 px-4 border-2 border-gray-700 bg-white focus:outline-none focus:bg-gray-100"
                      value={thisPost.needed_posts}
                      min={1}
                      placeholder="1"
                      disabled
                    />
                  </div>
                  {/* <div className="flex flex-col">
                    <label>Occupation (number)</label>
                    <input
                      type="number"
                      className="appearance-none rounded w-full py-1 px-4 border-2 border-gray-700 bg-white focus:outline-none focus:bg-gray-100"
                      value={thisPost.occupied_posts}
                      min={1}
                      placeholder="1"
                      disabled
                    />
                  </div>
                  <div className="flex flex-col">
                    <label>Explanation</label>
                    <textarea
                      className="appearance-none rounded w-full py-1 px-4 border-2 border-gray-700 bg-white focus:outline-none focus:bg-gray-100"
                      placeholder="Explanation"
                      value={thisPost.description}
                      rows={2}
                      disabled
                    ></textarea>
                  </div> */}
                  <div className="flex flex-col">
                    <label>Aproved posts</label>
                    <input
                      type="number"
                      className="appearance-none rounded w-full py-1 px-4 border-2 border-gray-700 bg-white focus:outline-none focus:bg-gray-100"
                      value={thisPost.confirmed_posts}
                      placeholder="number of aproved posts"
                      disabled
                    />
                  </div>
                  {/* <div className="flex flex-col">
                    <label>Aproved date</label>
                    <input
                      type="text"
                      className="appearance-none rounded w-full py-1 px-4 border-2 border-gray-700 bg-white focus:outline-none focus:bg-gray-100"
                      value={
                        thisPost.approval_date
                          ? DATE(thisPost.approval_date.toString())
                          : ""
                      }
                      placeholder="YYYY/MM/DD"
                      disabled
                    />
                  </div> */}

                  <div className="flex flex-col">
                    <label>Received posts (number)</label>
                    <input
                      type="number"
                      className={`appearance-none rounded w-full py-1 px-4 border-2 bg-white focus:outline-none focus:bg-gray-100 ${
                        formError !== null &&
                        formError.element === "received_posts"
                          ? "border-red-500"
                          : "border-gray-700"
                      }`}
                      value={receivedPosts}
                      onChange={(e) => {
                        setReceivedPosts(Number(e.target.value));
                      }}
                      min={0}
                      max={thisPost.confirmed_posts}
                      placeholder="1"
                      disabled={loadingRequest}
                    />
                    {formError !== null &&
                      formError.element === "received_posts" && (
                        <span className="text-red-600 font-bold text-xs">
                          {formError.msg}
                        </span>
                      )}
                  </div>
                </div>
                <div className="pt-4 flex justify-between">
                  <button
                    className="btn bg-gray-400 text-white hover:bg-gray-600"
                    onClick={() => {
                      setThisPost(null);
                      setReceivedPosts(0);
                      setModalState(null);
                    }}
                  >
                    Exit
                  </button>
                  <button
                    className="btn bg-primary text-white hover:bg-blue-600"
                    disabled={loadingRequest}
                  >
                    {loadingRequest ? "Loading..." : "Request"}
                  </button>
                </div>
              </div>
            </form>
          </div>
        </Modal>
      )}
      {/* End of Upload Modal */}

      {viewPost && modalState === null && thisPost && props.systemPositions && (
        <Modal
          backDrop={true}
          theme={Themes.default}
          close={(): void => {
            setThisPost(null);
            setViewPost(false);
          }}
          displayClose={true}
          backDropClose={false}
          title={
            <div className="flex flex-row gap-4 items-center">
              {/* <IoBriefcase className="text-5xl text-gray-500" /> */}
              <div className="flex flex-col">
                <span className="font-bold text-2xl text-gray-500">
                  Viewing Post Request
                  {/* on{" "}
                  {
                    getPositionById(props.systemPositions, thisPost.position_id)
                      .position_title
                  } */}
                </span>
              </div>
            </div>
          }
          widthSizeClass={ModalSize.large}
          padding={{ title: true, body: false, footer: false }}
        >
          <section className="px-4 mx-auto mb-4 mt-3 flex items-start">
            <section className="w-full">
              <div className="flex flex-row item-center justify-left gap-3 mb-2">
                <span className="text-gray-500 italic">Position: </span>
                <span className="font-bold">
                  {
                    getPositionById(props.systemPositions, thisPost.position_id)
                      .position_title
                  }
                </span>
              </div>
              <div className="flex flex-row item-center justify-left gap-3 mb-2">
                <span className="text-gray-500 italic">Requested posts: </span>
                <span className="font-bold text-xl">
                  {thisPost.needed_posts}
                </span>
              </div>
              <div className="flex flex-row item-center justify-left gap-3 mb-2">
                <span className="text-gray-500 italic">Confirmed posts: </span>
                <span className="font-bold text-xl">
                  {thisPost.requested_post_status ===
                  PostRequestStatusInterface.CONFIRMED ? (
                    <div className="flex items-center gap-1">
                      {thisPost.confirmed_posts}
                      <BiReply className="bg-yellow-600 w-5 h-5 text-white cursor-pointer" />
                    </div>
                  ) : (
                    "-"
                  )}
                </span>
              </div>
              <div className="flex flex-row item-center justify-left gap-3 mb-2">
                <span className="text-gray-500 italic">Received posts: </span>
                <span className="font-bold text-xl">
                  {thisPost.requested_post_status ===
                  PostRequestStatusInterface.RECEIVED
                    ? thisPost.received_posts
                    : "-"}
                </span>
              </div>
              <div className="flex flex-row item-center justify-left gap-3 mb-2">
                <span className="text-gray-500 italic">Number of female: </span>
                <span className="font-bold text-xl">
                  {thisPost.female_number}
                </span>
              </div>
              <div className="flex flex-row item-center justify-left gap-3 mb-2">
                <span className="text-gray-500 italic">Number of male: </span>
                <span className="font-bold text-xl">
                  {thisPost.male_number}
                </span>
              </div>
              <div className="flex flex-row item-center justify-left gap-3 mb-2">
                <span className="text-gray-500 italic">Request reason: </span>
                <span className="font-bold text-xl">
                  {thisPost.description}
                </span>
              </div>
              <div className="flex flex-row item-center justify-left gap-3 mb-2">
                <span className="text-gray-500 italic">Status: </span>
                <span
                  className={`px-2 py-1 mx-1 font-semibold rounded ${
                    thisPost.requested_post_status ===
                    PostRequestStatusInterface.INITIATED
                      ? "text-gray-700 bg-gray-100 dark:bg-gray-700 dark:text-gray-100"
                      : thisPost.requested_post_status ===
                        PostRequestStatusInterface.CONFIRMED
                      ? "text-blue-700 bg-blue-100 dark:bg-blue-700 dark:text-blue-100"
                      : "text-green-800 bg-green-100 dark:bg-green-800 dark:text-green-100"
                  }`}
                >
                  {thisPost.requested_post_status ===
                  PostRequestStatusInterface.INITIATED
                    ? "Pending"
                    : capitalizeFirstLetter(thisPost.requested_post_status)}
                </span>
              </div>
            </section>
            <div className="mb-3 flex items-center justify-end gap-2">
              <button
                className="font-bold py-1 px-4 rounded bg-gray-500 text-white hover:bg-gray-700"
                onClick={() => {
                  setModalState("UPDATE");
                  setPostId(thisPost.requested_post_id);
                  setPositionId(thisPost.position_id);
                  setRequestedPosts(thisPost.needed_posts);
                  setRequestExplanation(thisPost.description);
                  setFemaleOnPosition(thisPost.female_number);
                  setMaleOnPosition(thisPost.male_number);
                  setFormSteps({ ONE: true, TWO: false });
                  setStepState({ ONE: false, TWO: false });
                }}
                disabled={
                  thisPost.requested_post_status !==
                  PostRequestStatusInterface.INITIATED
                }
              >
                Edit
              </button>
            </div>
          </section>
        </Modal>
      )}
    </React.Fragment>
  );
};

const mapStateToProps = ({
  auth,
  schools,
  system_settings,
}: StoreState): {
  auth: Auth;
  schools: SchoolsInterface[];
  schoolPosts: PostSchoolInterface[];
  systemPositions: PositionAPI[] | null;
} => {
  return {
    auth: auth,
    schools: schools.schools,
    schoolPosts: schools.schoolPosts,
    systemPositions: system_settings.systemPositions,
  };
};

export const PostRequest = connect(mapStateToProps, {
  getPositions,
  getSchools,
  fetchSchoolPosts,
  createSchoolPost,
  updateSchoolPost,
  PushPopupAlert,
})(RequestTeacher);
