import React from "react";
import Table from "../../../components/Table/Table";
import { PositionRequirementTableInterface } from "./ViewPosition";

interface RequirementProps {
  DistinctSubCategories: PositionRequirementTableInterface[];
  requirements: PositionRequirementTableInterface[];
}

const ViewRequirements = (props: RequirementProps) => {
  const tableData = (): JSX.Element[] => {
    let data: PositionRequirementTableInterface[] = props.requirements;
    if (data.length === 0) {
      return [
        <tr key={153} className="border-b border-gray-200 hover:bg-gray-100">
          <td className="p-3 text-center" colSpan={3}>
            No Data Found
          </td>
        </tr>,
      ];
    }

    return data.map((item, index) => (
      <tr
        key={index + item.field_id}
        className="border-b border-gray-200 hover:bg-gray-100"
      >
        <td className="p-3 text-left whitespace-nowrap">{index + 1}</td>
        <td className="p-3 text-left">
          <span>
            {props.DistinctSubCategories.filter(
              (elem) => elem.subcategory_id === item.subcategory_id
            ).length > 0 &&
              props.DistinctSubCategories.filter(
                (elem) => elem.subcategory_id === item.subcategory_id
              )[0].subcategory_title}{" "}
            in {item.field_title}
          </span>
        </td>
        <td className="p-3 text-left text-lg italic">
          {item.required ? (
            <span className="text-yellow-800">Required</span>
          ) : (
            "Optional"
          )}
        </td>
      </tr>
    ));
  };

  return (
    <div>
      <Table
        columns={[
          { columnTitle: "#", alignText: "left", padding: "p-3" },
          { columnTitle: "Document", alignText: "left", padding: "p-3" },
          { columnTitle: "Status", alignText: "left", padding: "p-3" },
          // { columnTitle: "", alignText: "left", padding: "p-3" },
        ]}
        rows={tableData}
        pagination={false}
        search={false}
        thColor="gray"
      />
    </div>
  );
};

export default ViewRequirements;
